import React, { Component } from 'react';
import Link from 'react-router-dom/Link';
// import jQuery from 'jquery';
// import { ButtonToolbar, SplitButton, MenuItem } from 'react-bootstrap';

class Header extends Component {

    constructor() {

        super();

        this.state = {
        	showMenu: false,
            selected: 1,
            headerClass: 'navbar-toggle collapsed animated '
        };
    };

	isActive(value) {
	    // return((value===this.state.selected) ?'active':' not-active');
	    return((value===this.props.tab) ?'active':' not-active');
	};

	toggleMenu() {
		this.setState({
			showMenu: !this.state.showMenu
		});
	};

	setTab(thisTab) {
	    this.setState({selected  : thisTab})
	};

  	render() {
	    return (
			<div className="header-container" id="header-container">
				<nav className="navbar navbar-default">
					<div className="container-fluid">
						<div className="navbar-header">
							<button type="button" className="navbar-toggle collapsed" onClick={this.toggleMenu.bind(this)}>
							<span className="sr-only">Toggle navigation</span>
							<span className="icon-bar"></span>
							<span className="icon-bar"></span>
							<span className="icon-bar"></span>
							</button>
							<Link to="#" className="navbar-brand">Jay Depala </Link>
						</div>
						<div className={this.state.showMenu ? "navbar-collapse" : "collapse navbar-collapse"}>
							<ul className="nav navbar-nav">
								<li className={this.isActive('1')} onClick={this.setTab.bind(this, '1')}>
									<Link to="/home">
										Home <span className="sr-only">(current)</span>
									</Link>
								</li>
								<li className={this.isActive('3')} onClick={this.setTab.bind(this, '3')}>
									<Link to="/calculator">
										Calculator
									</Link>
								</li>
								<li className={this.isActive('4')} onClick={this.setTab.bind(this, '4')}>
									<Link to="/series-listing">
										Series
									</Link>
								</li>
								<li className={this.isActive('2')} onClick={this.setTab.bind(this, '2')}>
									<Link to="/about">
										About
									</Link>
								</li>
							</ul>
						</div>
					</div>
				</nav>
			</div>
	    );
  	}
}

export default Header;



