import React, { Component } from 'react';
import Scroll 				from 'react-scroll';

var Link       = Scroll.Link;
// var DirectLink = Scroll.DirectLink;
// var Element    = Scroll.Element;
var Events     = Scroll.Events;
var scroll     = Scroll.animateScroll;
var scrollSpy  = Scroll.scrollSpy;

class SmoothScroll extends Component {	

    constructor() {
        super();

        this.scrollToTop = this.scrollToTop.bind(this);
    };

	componentDidMount() {

		Events.scrollEvent.register('begin', function() {
		  // console.log("begin", arguments);
		});

		Events.scrollEvent.register('end', function() {
		  // console.log("end", arguments);
		});

		scrollSpy.update();
	};

	scrollToTop() {
		scroll.scrollToTop();
	};

	componentWillUnmount() {
		Events.scrollEvent.remove('begin');
		Events.scrollEvent.remove('end');
	};

	render() {

		if(this.props.type === "up")
		{
			return (
				<div className="smooth-scroll">
					<Link to="app-header" spy={true} smooth={true} duration={500}
						activeClass="hide"
						id="back-to-top"
					    className="btn btn-info btn-lg back-to-top" 
					    role="button" 
					    title="Click to return on the top page" 
					    data-toggle="tooltip" 
					    data-placement="left">
					    
					    <span className="fa fa-angle-up"></span>

					</Link>
				</div>
			);
		}
		else {		
			return (
				<div className="smooth-scroll"></div>
			);
		}

	}
};

export default SmoothScroll;


