import React, { Component } from 'react';
import Header   			from '../partials/Header';

class Calculator extends Component {

	constructor() {

	    super();

	    this.state = {
	    	num1: null,
	    	num2: null,
	    	sign: '',
	    	answer: null,
	    	showPopUp: null,
	    	friendPopUp: {
	    		isHidden: null,
	    		msg: {
		    		greeting: '',
		    		description: ''
	    		}
	    	}
	    };

	    this.clearAll = this.clearAll.bind(this);
	    this.calculate = this.calculate.bind(this);
	    this.numberClicked = this.numberClicked.bind(this);
	    this.signClicked = this.signClicked.bind(this);
	};

	clearAll() {
		this.setState({
	    	num1: null,
	    	num2: null,
	    	sign: '',
	    	answer: null,
	    	showPopUp: null,
	    	friendPopUp: {
	    		isHidden: null,
	    		msg: {
		    		greeting: '',
		    		description: ''
	    		}
	    	}
	    });
	};

	togglePopUp() {
		return (this.state.showPopUp === true ? 'alert alert-dismissible alert-danger animated flipInX' : this.state.showPopUp === null ? 'hide' : 'alert alert-dismissible alert-danger animated flipOutX' );
	};

	toggleFriendPopUp() {
		return (this.state.friendPopUp.isHidden === true ? 'alert alert-dismissible alert-success animated flipInY' : this.state.friendPopUp.isHidden === null ? 'hide' : 'alert alert-dismissible alert-success animated flipOutY' );
	};

	changePopUp() {
		this.setState({
	    	showPopUp: !this.state.showPopUp,
	    	friendPopUp: {
	    		isHidden: null,
	    		msg: {
		    		greeting: '',
		    		description: ''
	    		}
	    	}
	    });
	};

	changeFriendPopUp(thisFriend) {

		var msgObj = {};

		if(!this.state.friendPopUp.isHidden)
		{
			var welcMsg = [
				{
					'greeting': 'Hi {name}!',
					'description': 'Glad to see you\'re using this!'
				},
				{
					'greeting': 'Hello {name}!',
					'description': 'Do tell me if you liked the app'
				},
				{
					'greeting': 'Sup {name}?',
					'description': 'Did you like the app? No! :(!'
				},
				{
					'greeting': 'Hey {name}?',
					'description': 'You\'re still here! That must be a good sign!'
				},
				{
					'greeting': 'Salut {name}!',
					'description': 'I\'ll sell this app for $50 mil'
				},
				{
					'greeting': 'Gesundheit {name}!',
					'description': 'That\'s hi in German!'
				},
				{
					'greeting': 'Bonjour {name}!',
					'description': 'That\'s hi in French!'
				},
				{
					'greeting': 'Hi {name}!',
					'description': 'Do tell me how the app turns out for you?'
				},
				{
					'greeting': 'Hi {name}!',
					'description': 'I think you\'re liking this!'
				},
				{
					'greeting': 'Hi {name}!',
					'description': 'You know, after some time the messages will start to repeat?'
				},
				{
					'greeting': 'Hey {name}!',
					'description': 'It\'s so easy to amuse you!'
				},
				{
					'greeting': 'Hey {name}!',
					'description': 'You know, you can send this app to more people right?'
				},
				{
					'greeting': 'Hi {name}!',
					'description': 'You\'ve tried so hard and got so far, but in the end, it doesn\'t even matter!'
				},
				{
					'greeting': 'Hey {name}!',
					'description': 'I was doing just fine before I met you! I drink too much and that\'s an issue!'
				}
			];

			var randGreeting = welcMsg[Math.floor(Math.random()*welcMsg.length)];

			randGreeting.greeting = randGreeting.greeting.replace('{name}', thisFriend.name);

			msgObj = randGreeting;
		}
		else
		{
			msgObj = this.state.friendPopUp.msg
		}

		this.setState({
	    	showPopUp: null,
	    	friendPopUp: {
	    		isHidden: !this.state.friendPopUp.isHidden,
	    		msg: msgObj
	    	}
	    });
	};

	isUserAFriend(firstNumber, secondNumber) {

		var friendsIndex = [
			{
				'no': 9930472601,
				'name': 'Jai Depala'
			},
			{
				'no': 9323274049,
				'name': 'Jitu Bhai'
			},
			{
				'no': 7710082823,
				'name': 'Kiran Depala'
			},
			{
				'no': 8652849226,
				'name': 'RP'
			},
			{
				'no': 9029117520,
				'name': 'Anjali'
			},
			{
				'no': 7675824450,
				'name': 'Janu'
			},
			{
				'no': 9819803878,
				'name': 'Maulik Thakkar'
			},
			{
				'no': 7045653878,
				'name': 'Neha'
			},
			{
				'no': 9664880014,
				'name': 'Sonam'
			},
			{
				'no': 7738100391,
				'name': 'Sneha'
			},
			{
				'no': 9970779736,
				'name': 'Manaswee'
			},
			{
				'no': 8655020304,
				'name': 'Yogesh'
			},
			{
				'no': 9702570967,
				'name': 'Dalbir 22'
			},
			{
				'no': 9594622340,
				'name': 'VP'
			},
			{
				'no': 9920129263,
				'name': 'Rohan'
			},
			{
				'no': 9870846845,
				'name': 'Baahubali Uday'
			},
			{
				'no': 9892128358,
				'name': 'Nikhil'
			},
			{
				'no': 9773126449,
				'name': 'Vaibhav'
			},
			{
				'no': 9930673807,
				'name': 'Rushab'
			}
		];

		for (var i = 0, len = friendsIndex.length; i < len; i++)
		{
			var thisFriend = friendsIndex[i];

			if(thisFriend.no === firstNumber || thisFriend.no === secondNumber)
				return thisFriend;
		}

		return false;
	};

	calculate() {

		var firstNumber = parseInt(this.state.num1, 10);
		var secondNumber = parseInt(this.state.num2, 10);

		var theSign = this.state.sign;

		if((!firstNumber && firstNumber !== 0) || (!secondNumber && secondNumber !== 0)) {
			this.clearAll();
			return false;
		}

		var thisFriend = this.isUserAFriend(firstNumber, secondNumber);

		if(thisFriend)
		{
			this.changeFriendPopUp(thisFriend);
		}

		switch(theSign) {
			case '+':
						this.setState({
							answer: firstNumber + secondNumber
						});

						break;
			case '-':

					this.setState({
						answer: firstNumber - secondNumber
					});

					break;
			case '÷':

					if(secondNumber === 0)
					{
						this.setState({
							answer: '∞'
						});
					}
					else
					{					
						this.setState({
							answer: parseFloat((firstNumber / secondNumber).toFixed(4))
						});
					}


					break;
			case 'x':

					this.setState({
						answer: firstNumber * secondNumber
					});

					break;
			default:
					this.setState({
				    	num1: null,
				    	num2: null,
				    	sign: '',
						answer: null
					});

					break;
		}
	};

	numberClicked(newNumber) {

		this.setState({
		
			answer: this.state.answer
		
		}, function stateUpdateComplete() {

			if(this.state.answer && this.state.answer !== null )
			{
				this.clearAll();
			}

		}.bind(this));

		if(this.state.sign && this.state.sign !== '')
		{
			// var addedNum = this.state.num2 ? this.state.num2 + '' + newNumber : newNumber;

			this.setState({
				num2: this.state.num2 ? this.state.num2 + '' + newNumber : newNumber
			});
		}
		else
		{
			// var addedNum = this.state.num1 ? this.state.num1 + '' + newNumber : newNumber;

			this.setState({
				num1: this.state.num1 ? this.state.num1 + '' + newNumber : newNumber
			});
		}
	};

	signClicked(getSign) {
		if(!this.state.num1 || this.state.num1 === null)
			return false;

		this.setState({
		
			sign: getSign
		
		}, function stateUpdateComplete() {

			if((this.state.num1 !== null) && (this.state.num2 !== null))
			{
				this.calculate();
			}

		}.bind(this));

	};

	isActive(value) {
	    return ((value===this.state.sign) ? 'active operator' : 'passive operator');
	};

    render() {
        return (

	    	<div className="bio-container">

		    	<Header tab="3" />

				<div id="calculator">
					<div className="top">
						<span className="clear" onClick={this.clearAll.bind(this)}>C</span>

						<div className="screen"> {this.state.num1}  {this.state.sign}  {this.state.num2}  {this.state.answer !== null ? " = " + this.state.answer : ""} </div>
					</div>
					
					<div className="keys">
						<span onClick={this.numberClicked.bind(this, 7)}>7</span>
						<span onClick={this.numberClicked.bind(this, 8)}>8</span>
						<span onClick={this.numberClicked.bind(this, 9)}>9</span>
						<span className={this.isActive('+')} onClick={this.signClicked.bind(this, '+')}>+</span>

						<span onClick={this.numberClicked.bind(this, 4)}>4</span>
						<span onClick={this.numberClicked.bind(this, 5)}>5</span>
						<span onClick={this.numberClicked.bind(this, 6)}>6</span>
						<span className={this.isActive('-')} onClick={this.signClicked.bind(this, '-')}>-</span>

						<span onClick={this.numberClicked.bind(this, 1)}>1</span>
						<span onClick={this.numberClicked.bind(this, 2)}>2</span>
						<span onClick={this.numberClicked.bind(this, 3)}>3</span>
						<span className={this.isActive('÷')} onClick={this.signClicked.bind(this, '÷')}>÷</span>

						<span onClick={this.numberClicked.bind(this, 0)}>0</span>
						<span onClick={this.changePopUp.bind(this)}>.</span>
						<span onClick={this.calculate.bind(this)} className="eval">=</span>
						<span className={this.isActive('x')} onClick={this.signClicked.bind(this, 'x')}>x</span>
					</div>
				</div>

				<div className={this.togglePopUp()}>
					<button type="button" className="close" data-dismiss="alert" onClick={this.changePopUp.bind(this)}>&times;</button>
					<h4>Alert!</h4>
					<p>The "." key still doesn&#39;t do shit!</p>
				</div>

				<div className={this.toggleFriendPopUp()}>
					<button type="button" className="close" data-dismiss="alert" onClick={this.changeFriendPopUp.bind(this)}>&times;</button>
					<h4>{ this.state.friendPopUp.msg.greeting }</h4>
					<p>{ this.state.friendPopUp.msg.description }</p>
				</div>

			</div>

        );
    };
}

export default Calculator;


