import React, { Component } from 'react';
import LazyLoad 			from 'react-lazy-load';

class GetImage extends Component {
   
	constructor(props) {
		super(props);
		this.state = { imageStatus: 'loading', showIcon: false };
	}

  	handleImageLoaded() {
    	this.setState({ imageStatus: 'loaded', showIcon: true });
  	}
 
  	handleImageErrored() {
    	this.setState({ imageStatus: 'failed to load' });
  	}

  render() {

    var styles = {
        show: {
            display: "none"
        },
        hide: {
        	display: "block"
        }
    };

    return (
		<div className="bs-image-component">
			<LazyLoad height='auto' width='auto' className="bs-image">
				<img  	src={ this.props.img } 
						onLoad={ this.handleImageLoaded.bind(this) } 
						onError={ this.handleImageErrored.bind(this) } 
						className={ this.props.className } 
						alt={ this.props.name } 
						title={ this.props.name } />
			 </LazyLoad>
	 		<span style={ this.state.showIcon ? styles.hide : styles.show } className="play-button-icon"><i className="fa fa-play fa-3x" aria-hidden="true"></i></span>
		</div>
    );
  }
}

export default GetImage;


