// Libraries
    import React, {Component} from 'react';
    // import { createBrowserHistory } from 'history';
    // import { BrowserRouter as Router } from 'react-router-dom';
    // import { Route, IndexRoute, Link, browserHistory } from 'react-router';
    // import { IndexRoute, browserHistory } from 'react-router';
    import { BrowserRouter as Router, Route /*, Link, withRouter */} from 'react-router-dom';

// CSS
    import './style/App.css';

// Components
    import Home             from './components/main/Home';
    import About            from './components/main/About';
    import Calculator       from './components/main/Calculator';
    import Listing          from './components/main/Listing';
    import SeriesLanding    from './components/main/SeriesLanding';

class App extends Component {

    constructor() {

        super();

        this.state = {
            currentTab: 1
        };
    };

    render() {
        return (
            <div className="app-container">
                <Router history={history}>
                    <div className="container-fluid">

                        <Route  name="calculator"
                                exact={true} 
                                path="/" 
                                component={Calculator}>
                        </Route>

                        <Route  name="home"
                                exact={true} 
                                path="/home" 
                                component={Home}>
                        </Route>

                        <Route  name="about"
                                exact={true} 
                                path="/about" 
                                component={About}>
                        </Route>

                        <Route  name="calculator"
                                exact={true} 
                                path="/calculator" 
                                component={Calculator}>
                        </Route>

                        <Route  name="listing"
                                exact={true} 
                                path="/series-listing" 
                                component={Listing}>
                        </Route>

                        <Route  name="landing"
                                exact={true} 
                                path="/series-listing/:seriesName" 
                                component={SeriesLanding}>
                        </Route>

                    </div>
                </Router>
            </div>
        );
    }
}

export default App;


