import React, { Component } from 'react';
import Header   			from '../partials/Header';
import SeriesSeasonsListing from '../partials/SeriesSeasonsListing';

class SeriesLanding extends Component {

	getParams() {
		return this.props.match.params.seriesName;
	};

  	render() {

  		var theList = ['Season 1', 'Season 2', 'Season 3']

        const seriesListing = theList.map(function(item) {
            return (
                    <SeriesSeasonsListing key={item} id={item} />
            );
        });

	    return (
			<div className="series-landing-container">

				<Header tab="4" />

				<div className="panel panel-default">

					<div className="panel-heading">
					    <h3 className="panel-title">
					    	{ this.getParams() }
					    </h3>
					</div>

			        <ul className="list-group">
			        	{ seriesListing }
			        </ul>

				</div>

			</div>
	    );
  	}
}

export default SeriesLanding;



