import React, { Component } from 'react';
import Header   			from '../partials/Header';

class Home extends Component {

	constructor() {

	    super();

	    this.state = {
	    	timesNo: 1,
	        theNo: 0,
	    };
	};

	addNumber() {
		this.setState({
		    theNo: this.state.theNo+this.state.timesNo
		});
	};

	subtractNumber() {
		this.setState({
		    theNo: this.state.theNo-this.state.timesNo
		});
	};

	changeTimes(event) {
		this.setState({
			timesNo: parseInt(event.target.value, 10)
		});
	};

	changeSelect(event) {
		this.setState({
			selectNo: event.target.value
		});
	};

  	render() {
	    return (
			<div className="home-container">

                <Header tab="1" />

				<h4 className="text-center animated zoomInUp"> {this.state.theNo} </h4>

				<div className="form-group">
					<input 
						className="form-control" 
						id="focusedInput" 
						type="number" 
						min="1" 
						defaultValue={this.state.timesNo} 
						onChange={this.changeTimes.bind(this)} />
				</div>

				<div className="btn-group btn-group-justified">
				  	<a 
				  		href="#" 
				  		className="btn btn-default"
				  		onClick={this.addNumber.bind(this)}>
				  		Add
				  	</a>
				  	<a
				  		href="#" 
				  		className="btn btn-default"
				  		onClick={this.subtractNumber.bind(this)}>
			  			Subtrack
		  			</a>
				</div>


				<div className="form-group">
					<label htmlFor="reactSelect" className="col-lg-2 control-label">Select Value: {this.state.selectNo}</label>
					<div className="col-lg-10">
						<select className="form-control" id="reactSelect" onChange={this.changeSelect.bind(this)}>
							<option>1</option>
							<option>2</option>
							<option>3</option>
							<option>4</option>
							<option>5</option>
						</select>
					</div>
				</div>

			</div>
	    );
  	};
}

export default Home;



